<template>
  <div class="dialog-root" v-if="show" @click.self="show = false">
    <div class="dialog-view">
      <div class="dialog-title">温馨提示</div>
      <div class="dialog-content">{{ content }}</div>
      <div class="dialog-foot">
        <div class="dialog-confirm" @click.stop="confirm">保存</div>
        <div class="dialog-cancel" @click.stop="close">取消</div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    dialogShow: {
      value: Boolean,
      default: () => false,
    },
  },
  data() {
    return {
      show: false,
      content:
        "普通弹窗内容弹窗内容弹窗内容弹窗内容弹窗内容弹窗内容弹窗内容弹窗内容弹窗内容",
    };
  },
  watch: {
    dialogShow: {
      handler(newVal) {
        this.show = newVal;
      },
      immediate: true,
    },
  },
  methods: {
    close() {
      this.show = false;
      this.$emit("close");
    },
    confirm() {
      this.show = false;
      this.$emit("confirm");
    },
  },
};
</script>
<style scoped lang="scss">
.dialog-root {
  z-index: 99;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(#202020, 0.8);

  .dialog-view {
    position: absolute;
    top: 50%;
    transform: translate(-50%, -50%);
    left: 50%;
    width: 336px;
    min-height: 197px;
    max-height: 350px;
    background: #ffffff;
    border-radius: 4px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 50px 30px 32px;

    .dialog-title {
      font-family: "PingFang SC";
      font-style: normal;
      font-weight: 400;
      text-align: center;
      font-size: 18px;
      line-height: 26px;
      color: #1a1a1a;
    }

    .dialog-content {
      //   width: 100%;
      display: inline;
      text-align: left;
      margin: 15px auto;
      font-weight: 500;
      font-size: 14px;
      line-height: 22px;
      color: #b3b3b3;
    }

    .dialog-foot {
      width: 100%;
      padding: 0 10px;
      display: flex;
      justify-content: space-between;
      align-items: center;

      .dialog-confirm {
        cursor: pointer;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 112px;
        height: 37px;
        background-color: #f50537;
        border-radius: 2px;
        color: #ffffff;
      }
      .dialog-confirm:active {
        background-color: #fa335c;
        opacity: 0.95;
      }

      .dialog-cancel {
        cursor: pointer;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 112px;
        height: 37px;
        color: #000000;
        border-radius: 2px;
        border: 1px solid #dddddd;
      }

      .dialog-cancel:active {
        color: #4a4a4a;
        opacity: 0.95;
      }
    }
  }
}
</style>
